import React from 'react';
import s from './AdaptiveMenu.module.css'
import {NavLink} from "react-router-dom";
import close from '../../assets/images/крестик.png'

const AdaptiveMenu = (props) => {

    let onClick = () => {
        return document.getElementById('div').style.display = 'none';
    }

    return (
        <div className={s.adaptiveMenu} id={'div'} style={{display: 'block'}} onClick={props.setMenu}>
            <div onClick={onClick}>
                <div onClick={props.setMenu}>
                    <img className={s.close} src={close} alt=""/>
                </div>
                <nav className={s.navbar}>
                    <div className={s.nav + ' ' + s.first}>
                        <hr className={s.hr}/>
                        <NavLink to='/main' activeClassName={s.activeLink}>Главная</NavLink>
                    </div>
                    <div className={s.nav}>
                        <hr className={s.hr}/>
                        <NavLink to='/news' activeClassName={s.activeLink}>Новости</NavLink>
                    </div>
                    <div className={s.nav}>
                        <hr className={s.hr}/>
                        <NavLink to='/biography' activeClassName={s.activeLink}>Биография</NavLink>
                    </div>
                    <div className={s.nav}>
                        <hr className={s.hr}/>
                        <NavLink to='/books' activeClassName={s.activeLink}>Книги</NavLink>
                    </div>
                    <div className={s.nav}>
                        <hr className={s.hr}/>
                        <NavLink to='/video' activeClassName={s.activeLink}>Видео</NavLink>
                    </div>
                </nav>
            </div>
        </div>
    );
};

export default AdaptiveMenu;