import React from 'react';
import s from './Navbar.module.css';

const NavbarAdaptive = (props) => {

    return (
        <div className={s.navbarAdaptive} onClick={props.setMenu}>
            <div className={s.line}></div>
            <div className={s.line}></div>
            <div className={s.line}></div>
        </div>
    );
};

export default NavbarAdaptive;