import './App.css';
import Header from "./components/Header/Header";
import Navbar from "./components/Navbar/Navbar";
import {Switch, Route, Redirect, HashRouter} from 'react-router-dom';
import Main from "./components/Main/Main";
import Footer from "./components/Footer/Footer";
import React, {useState} from 'react';
import {withSuspense} from "./hoc/WithSuspense";
import {Provider} from "react-redux";
import store from "./redux/store-redux";
import NavbarAdaptive from "./components/Navbar/NavbarAdaptive";
import AdaptiveMenu from "./components/AdaptiveMenu/AdaptiveMenu";

const Biography = React.lazy(() => import('./components/Biography/Biography.js'));
const BooksContainer = React.lazy(() => import('./components/Books/BooksContainer.js'));
const VideosContainer = React.lazy(() => import('./components/Video/VideosContainer.js'));
const NewsContainer = React.lazy(() => import('./components/News/NewsContainer.js'));

const App = () => {

    let [editMode, setEditMode] = useState();

    let setMenu = () => {
        setEditMode(!editMode);
    }

    return (
        <HashRouter>
            <Provider store={store}>
                <div className='global'>
                    <div className='line'/>
                    {editMode &&
                        <div className='menu'>
                            <AdaptiveMenu setMenu={setMenu}/>
                        </div>
                    }
                    <div className='App'>
                        <div className='navbar2'><NavbarAdaptive setMenu={setMenu}/></div>
                        <div className='h'>
                            <header><Header/></header>
                        </div>
                        <div className='navbar'><Navbar/></div>
                        <div className='content'>
                            <Switch>
                                <Route exact path={'/'}
                                       render={() => <Redirect to={'/main'}/>}/>
                                <Route path={'/main'} render={() => <Main/>}/>
                                <Route path={'/biography'} render={withSuspense(Biography)}/>
                                <Route path={'/books'} render={withSuspense(BooksContainer)}/>
                                <Route path={'/video'} render={withSuspense(VideosContainer)}/>
                                <Route path={'/news'} render={withSuspense(NewsContainer)}/>
                            </Switch>
                            <Route path={'*'} render={() => <div>
                                <footer className='footer_adaptive'><Footer/></footer>
                            </div>}/>
                        </div>
                    </div>
                </div>
                <div>
                    <footer className='footer'><Footer/></footer>
                </div>
            </Provider>
        </HashRouter>
    );
}


export default App;
